@charset "UTF-8";
/* Estilos para cartões e elementos de conteúdo */
.card {
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  height: 100%;
}
.card:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  transform: translateY(-5px);
}
.card .card-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.card .card-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card .card-body {
  padding: 1.5rem;
}
.card .card-title {
  font-size: 1.25rem;
  margin-bottom: 0.75rem;
  color: #800020;
}
.card .card-title a {
  color: inherit;
  text-decoration: none;
}
.card .card-title a:hover {
  color: #ffd700;
}
.card .card-subtitle {
  font-size: 0.9rem;
  color: #4a4a4a;
  margin-bottom: 1rem;
  font-weight: 500;
}
.card .card-text {
  color: #333333;
  margin-bottom: 1.25rem;
  line-height: 1.6;
}
.card .card-footer {
  padding: 1rem 1.5rem;
  background-color: #f8f8f8;
  border-top: 1px solid rgba(74, 74, 74, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card .card-footer .card-date {
  font-size: 0.85rem;
  color: #4a4a4a;
}
.card.card-primary {
  border-top: 3px solid #800020;
}
.card.card-secondary {
  border-top: 3px solid #ffd700;
}
.card.card-horizontal {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .card.card-horizontal {
    flex-direction: row;
  }
  .card.card-horizontal .card-img {
    width: 40%;
    height: auto;
  }
  .card.card-horizontal .card-body {
    width: 60%;
  }
}
.card.card-overlay {
  position: relative;
}
.card.card-overlay .card-img {
  height: 300px;
}
.card.card-overlay .card-body {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  color: #ffffff;
}
.card.card-overlay .card-body .card-title, .card.card-overlay .card-body .card-subtitle {
  color: #ffffff;
}
.card.card-overlay .card-body .card-text {
  color: rgba(255, 255, 255, 0.9);
}
.card.card-team {
  text-align: center;
}
.card.card-team .card-img {
  height: 250px;
  border-radius: 50%;
  width: 200px;
  margin: 1.5rem auto 0;
  overflow: hidden;
}
.card.card-team .card-title {
  margin-top: 0.5rem;
}
.card.card-team .social-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
}
.card.card-team .social-links a {
  color: #4a4a4a;
  transition: all 0.3s ease-in-out;
}
.card.card-team .social-links a:hover {
  color: #800020;
}